    .content {
    width: 100%;
    padding: 10px 0;
    .wrapper {
        width: 835px;
        margin: 0 auto;
    }
}

img {
    width: auto;
}

.item_about {
    &_img {
        float: left;
        width: 48%;
        margin-right: 56px;
        background-color: #eaf2f4;
        padding-bottom: 20px;
    }
    &_description {
        overflow: hidden;
        position: relative;
        float: right;
        width: 45%;
    }
}

.item_propose {
    &_title {
        margin: 65px 0 20px 0;
        h2 {
            font: 22px/33px 'OpenSansBold';
            text-transform: uppercase;
            color: #2c303d;
        }
    }
    overflow: hidden;
    clear: both;
    &_catalog {
        &_item {
            position: relative;
            float: left;
            width: 31.66%;
            padding-right: 2.5%;
            padding-bottom: 20px;
            img {
                width: 100%;
            }
            &:nth-child(3n) {
                padding-right: 0;
                .item_propose_catalog_item_price {
                    right: 0;
                }
            }
            &:nth-child(3n+1) {
                clear: both;
                .item_propose_catalog_item_price {
                    padding-right: 15px;
                    p {
                        text-decoration: line-through;
                        &:last-child {
                            padding-left: 15px;
                            text-decoration: none;
                            color: #ce2c52;
                        }
                    }
                }
            }
            &:nth-child(3n+2) {}
            &_price {
                position: absolute;
                display: inline-block;
                background: #8ae6cf;
                bottom: 42px;
                right: 21px;
                display: inline-block;
                background: #8ae6cf;
                p {
                    padding: 4px 2px 4px 4px;
                    text-align: center;
                    color: #2c303d;
                    font: 16px/16px "OpenSansBold";
                    display: inline-block;
                }
            }
        }
    }
}

.item {
    &_about {
        &_decription {
            &_title {
                h1 {
                    font: 24px/30px 'Oswald';
                    color: #2c303d;
                    text-transform: uppercase;
                }
            }
            &_code {
                margin: 20px 0 25px 0;
                p {
                    font: 10px/10px "OpenSans";
                    opacity: 0.7;
                    color: #2c303d;
                    font-weight: 400;
                    letter-spacing: .4px;
                }
            }
            &_price {
                p {
                    color: #2c303d;
                    font: 24px/27px "OpenSansBold";
                    display: inline-block;
                    text-decoration: line-through;
                    &:last-child {
                        text-decoration: none;
                        padding-left: 25px;
                        color: #ce2c52;
                    }
                }
            }
            &_ratig {
                margin-top: 25px;
                &_stars {
                    li {
                        &:hover {
                            background: url('./../images/star-active.png') no-repeat scroll center transparent;
                        }
                        width: 16px;
                        height: 16px;
                        background: url('./../images/star.png') no-repeat scroll center transparent;
                        background-size: contain;
                        display: block;
                        float: left;
                        overflow: hidden;
                        cursor: pointer;
                    }
                }
                &_link {
                    &_social {
                        &_block {
                            display: none;
                            background: red;
                        }
                        a {
                            font: 18px/18px "OpenSansBold";
                            color: #2c87ce;
                            text-decoration: none;
                            padding-left: 13px;
                            &:hover {
                                opacity: .75;
                            }
                        }
                    }
                }
            }
        }
    }
}

.item {
    &_about {
        &_decription {
            &_ratig {
                #rating {
                    position: relative;
                    float: left;
                    input~label {
                        background: url('./../images/star.png') no-repeat scroll center transparent;
                        background-size: contain;
                        width: 16px;
                        height: 16px;
                        position: relative;
                        float: right;
                        overflow: hidden;
                        cursor: pointer;
                    }
                    label:hover,
                    input:checked~label,
                    label:hover~label {
                        background: url('./../images/star-active.png') no-repeat scroll center transparent;
                        background-size: contain;
                        transition: .3s all;
                    }
                }
            }
            &_more {
                font: 12px/16px "OpenSans";
                color: #282828;
            }
            &_order {
                background-color: #8ae6cf;
                a {
                    font: 14px/14px 'OpenSans';
                    padding: 17px 56px;
                    color: #282828;
                }
            }
            &_size {
                p {
                    &:first-child {
                        font: 14px/33px 'OpenSansBold';
                        text-transform: uppercase;
                        color: #282d3b;
                        padding-right: 17px;
                    }
                    &:not(:first-child) {
                        width: 40px;
                        text-align: center;
                        height: 40px;
                        border: 1px solid rgba(0, 0, 0, 0.72);
                        -webkit-border-radius: 50%;
                        border-radius: 50%;
                        cursor: pointer;
                        font: 14px/40px 'OpenSansBold';
                        color: #282d3b;
                        margin-right: 11px;
                        &:hover {
                            background: #282d3b;
                            color: #fff;
                            transition: .3s all;
                        }
                    }
                    display: inline-block;
                }
            }
            &_ratig_link {
                &_socials {
                    &_block {
                        &:after {}
                        &:before {
                            content: "";
                            height: 7px;
                            width: 7px;
                            position: absolute;
                            background: #fff;
                            -webkit-transform: rotateX(90deg);
                            transform: rotate(45deg);
                            right: 50%;
                            top: -4.7px;
                            margin-right: -3.5px;
                            border-top: 1px solid #afafaf;
                            border-left: 1px solid #afafaf;
                            z-index: 3;
                        }
                        position: absolute;
                        background: #fff;
                        box-shadow: 0 0 5px rgba(0,
                        0,
                        0,
                        0.75);
                        z-index: 5;
                        right: 24%;
                        opacity: 0;
                        top:-999px;
                        transition: all 1s;
                        p {
                            font: 18px/18px "OpenSansBold";
                            color: #2c87ce;
                            text-decoration: none;
                            padding-left: 15px;
                            padding-bottom: 18px;
                            padding-top: 12px;
                        }
                        &_fb {
                            padding-bottom: 19px;
                            float: left;
                        }
                        &_twit {
                            float: left;
                        }
                        &_plink {
                            float: left;
                        }
                        &_yt {
                            float: left;
                        }
                        &_ig {
                            float: left;
                            padding-right: 15px;
                        }
                    }
                }
            }
        }
    }
}

.item_about_decription_more {
    margin: 30px 0 25px 0;
    clear: both;
}

#popup_social {
    padding-left: 17px;
}

.item_about_description_order {
    display: block;
    margin-top: 25px;
    a {
        display: block;
        margin-top: 25px;
        font: 14px/10px 'OpenSansBold';
        padding: 16px 0;
        color: #282828;
        text-transform: uppercase;
        text-decoration: none;
        background-color: #8ae6cf;
        vertical-align: middle;
        max-width: 214px;
        border: 1px solid #8ae6cf;
        text-align: center;
        border-radius: 30px;
        text-decoration: none;
        background-color: #8ae6cf;
        vertical-align: middle;
        &:hover {
            background: #fff;
            transition: .3s all;
        }
    }
}

#rating label:hover,
#rating input:checked~label,
#rating label:hover~label {
    background: url('./../images/star-active.png') no-repeat scroll center transparent;
    background-size: contain;
    transition: .3s all;
}

#rating input {
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
    opacity: 0;
    z-index: 0;
}

#raiting {
    position: relative;
    float: left;
}

.close {
    position: absolute;
    right: 15px;
    top: 15px;
    width: 15px;
    height: 15px;
    cursor: pointer;
}

.close:hover {
    opacity: 1;
}

.close:before,
.close:after {
    position: absolute;
    left: 15px;
    content: ' ';
    height: 15px;
    width: 2px;
    background-color: #2c87ce;
}

.close:before {
    transform: rotate(45deg);
}

.close:after {
    transform: rotate(-45deg);
}

.close:hover {
    opacity: .75;
}

.active {
    opacity: 1;
    top: 40%;
    transition: 1s all;
}

.selected {
    background: #282d3b;
    color: #fff;
    -webkit-transition: .3s all;
    -o-transition: .3s all;
    transition: .3s all;
}

.item_about_decription_size p:not(:first-child).selected {
    color: #fff;
}

.item_about_decription_title.head .item_about_decription_title_test {
        display: none;
    }


.item_about_description .item_about_decription_title {
    display: block;
}
.item_about_img{
	img{
		width: 100%;
	}
}
