@media only screen and (max-width: 768px) {
    .item_about_description {
        width: 40%;
    }
    .content {
        max-width: 835px;
        width: 100%;
    }
    .content .wrapper {
        width: auto;
        padding: 0 40px;
    }
    .item_propose_catalog_item {
        width: 47.5%;
    }
    .item_about_description {}
    .item_propose_catalog_item {
        padding-right: 0;
        margin-bottom: 5%;
        padding-bottom: 0;
    }
    .item_propose_catalog_item:nth-child(3n) {
        padding-right: 0;
    }
    .item_propose_catalog_item:nth-child(3n+2) {
        padding-right: 0;
    }
    .item_propose_catalog_item:nth-child(3n+1) {
        clear: none;
    }
    .item_propose_catalog_item:nth-child(3n+2) {
        padding-right: 0;
    }
    .item_propose_catalog_item:nth-child(even) {
        margin-left: 5%;
    }
    .item_propose_title h2 {
        text-align: center;
    }
    .item_about_decription_more {
        font: 12px/16px "OpenSans";
        color: #282828;
    }
    .item_propose_catalog_item_price {
        width: 100%;
        padding: 6px 0;
        padding-right: 0;
        right: 0;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        bottom: 0;
        opacity: 0.8;
    }
    .item_propose_catalog_item_price p {
        width: 100%;
        font: 22px/22px "OpenSansBold";
        text-align: center;
    }
    .item_propose_catalog_item:nth-child(3n+1) .item_propose_catalog_item_price p {
        &:first-child {
            display: none;
        }
    }
    .item_about_decription_size p:first-child {
        display: block;
        margin-bottom: 15px;
        &:before {
            content: 'select a ';
            font: 14px/14px 'OpenSansBold';
            margin-bottom: 15px;
        }
    }
    .item_about_decription_more:before {
        display: block;
        content: 'details:';
        font: 14px/14px 'OpenSansBold';
        text-transform: uppercase;
        margin-bottom: 15px;
    }
    .item_about_decription_ratig_link_socials_block {
        &_fb {
            float: left;
            clear: both;
            padding-bottom: 10px;
        }
        &_twit {
            float: left;
            clear: both;
            padding-bottom: 10px;
        }
        &_plink {
            float: left;
            clear: both;
            padding-bottom: 10px;
            overflow: hidden;
        }
        &_yt {
            float: left;
            clear: both;
            padding-bottom: 10px;
        }
        &_ig {
            display: block;
            float: left;
            clear: both;
            padding-bottom: 10px;
        }
    }
    #popup_social {
        display: none;
    }
    .item_about_decription_ratig_link_socials_block p {
        display: none;
    }
    .item_about_decription_ratig_link_socials_block {
        opacity: 1;
        top: 73px;
        background: transparent;
        border: none;
        box-shadow: none;
        left: 0;
        width: 10%;
    }
    .item_about_decription_ratig #rating {
        margin-bottom: 30px;
    }
    .close {
        display: none;
    }
    .item_about_description {
        position: static;
    }
    .item_about {
        position: relative;
    }
    .item_about_decription_ratig_link_socials_block:before {
        display: none;
    }
    .item_about_decription_ratig {
        margin-bottom: 30px;
    }
    .item_about_img {
        margin-right: 20px;
        width: 56%;
    }
    .item_about_decription_title {
        display: none;
    }
    .item_about .item_about_decription_title.head {
        display: block;
        margin-bottom: 30px;
    }
    .item_about_decription_code {
        margin-top: 0;
    }
    .item_about_description .item_about_decription_title {
        display: none;
    }
    .item_about_decription_title.head .item_about_decription_title_test {
        display: block;
    }
}
