@media only screen and (max-width:575px) {
    .content {
        width: 100%;
    }
    .content .wrapper {
        width: 100%;
        padding: 0;
    }
    .item_about_decription_title.head .item_about_decription_title_test {
        width: 100%;
        padding: 0 50px;
    }
    .item_about_img {
        width: auto;
        margin-right: 0;
    }
    .item_about_decription_ratig_link_socials_block {
        position: static;
    }
    .item_about_decription_title.head .item_about_decription_title_test {
        width: auto;
    }
    .item_about_img {
        width: auto;
        margin-right: 0;
        padding: 0 40px;
    }
    .item_about_description {
        position: static;
        width: auto;
        padding: 0 40px;
        margin-top: 20px;
    }
    .item_about_decription_ratig_link_social a {
        padding: 0;
    }
    .item_about_decription_ratig_link_socials_block {
        &_fb {
            padding-bottom: 19px;
            float: left;
            clear: none;
            width: 10%;
        }
        &_twit {
            float: left;
            clear: none;
            width: 10%;
        }
        &_plink {
            float: left;
            clear: none;
            width: 10%;
        }
        &_yt {
            float: left;
            clear: none;
            width: 10%;
        }
        &_ig {
            float: left;
            padding-right: 15px;
            clear: none;
            width: 10%;
        }
    }
    .item_about_decription_ratig {
        width: 100%;
    }
    .item_about_decription_ratig_link_social {
        width: 100%;
    }
    .item_about_decription_ratig_link_socials_block {
        position: static;
        width: 100%;
        clear: both;
    }
    .item_about_decription_ratig_link_socials_block div {
        display: inline-block;
        width: auto;
        padding-right: 16px;
    }
    .item_about_decription_ratig_link_socials_block div:last-child {
        padding-right: 0;
    }
    .item_about_decription_ratig_link_socials_block div.close {
        display: none;
    }
    .item_about_decription_more:before {
        display: none;
    }
    .item_about_decription_ratig_link_socials_block_fb {
        padding-bottom: 35px;
    }
    .item_about_decription_size {
        width: 100%;
        text-align: center;
    }
    .item_about_decription_size p:not(:first-child) {
        margin: 0 26px 30px 0;
    }
    .item_about_decription_size p:last-child {
        margin: 0;
    }
    .item_about_description_order a {
        margin: 0 auto;
    }
    .item_propose_catalog_item:nth-child(even) {}
    .item_propose_catalog_item {
        width: 280px;
        margin: 0 auto;
        padding: 0;
        clear: none;
        float: none;
    }
    .item_propose_catalog_item:nth-child(even) {
        margin-left: auto;
    }
    .item_slider {
        width: 100%;
    }
    .item_slider {
        width: 280px;
        margin: 0 auto;
        overflow: hidden;
        position: relative;
    }
    .item_slider_viewport {
        position: relative;
        left: 0;
        top: 0;
        width: 1800px;
    }
    .slide {
        width: 280px;
        height: 280px;
        float: left;
    }
    .prev {
        position: absolute;
        width: 29px;
        height: 50px;
        background: url('./../images/left.png');
        background-size: contain;
        z-index: 100;
        left: 0;
        top: 55%;
    }
    .next {
        top: 55%;
        right: 0;
        position: absolute;
        width: 29px;
        height: 50px;
        background: url('./../images/right.png');
        background-size: contain;
        z-index: 100;
    }
    .item_propose {
        position: relative;
        padding-bottom: 20px;
    }
    .item_propose_catalog_item_price {
        p {
            padding: 0;
        }
    }
}

@media only screen and (max-width:350px) {
    .item_about_decription_title.head .item_about_decription_title_test {
        padding: 0px 35px;
    }
    .item_about_decription_ratig_link_socials_block div {
        padding-right: 8px;
    }
    .item_about_decription_size p:not(:first-child) {
        margin: 0 15px 30px 0;
    }
    .item_slider {
        width: 260px;
    }
}
